ul,li{list-style-type:none;list-style-image:none;margin:0;padding:0}img{max-width:100%;height:auto;display:block}.left{float:left}.right{float:right}.clear{clear:both}.wysiwyg-selector ul li:before{content:"• "}.wysiwyg-selector ol{counter-reset:section}.wysiwyg-selector ol li:before{counter-increment:section;content:counter(section) " "}.wysiwyg-selector div.encadre{padding:1.3571em;border:1px solid #DBDBDB}.site-size-wrapper{width:100%;max-width:1350px;margin:0 auto;position:relative}#content-wrapper #content{float:none;width:100%}#content-wrapper.with-sidebar-first #content,#content-wrapper.with-sidebar-second #content{width:calc(100% - 300px);float:left}#content-wrapper.with-sidebar-first aside,#content-wrapper.with-sidebar-first.with-sidebar-second aside,#content-wrapper.with-sidebar-second aside{width:300px;float:left}#content-wrapper.with-sidebar-first.with-sidebar-second #content{width:calc(100% - 600px);float:left}
